.AbUs_component
{
	width: 25%;
	margin: 5% 0 0 6%;
	color: #CCCCCC;
}

.component__title
{
	font-size: 1.8vw;
}

.component__text
{
	font-size: 1.35vw;
	margin-top: 5%;
}

 @media screen and (max-width: 1024px)
 {
 	.component__title
 	{
 		font-size: 2.2vw;
 	}

 	.component__text
 	{
 		font-size: 2vw;
 	}
 }

 @media screen and (max-width: 764px)
 {
	
	.AbUs_component
	{
		width: 70%;
		margin-left: 15%;
	}

 	.component__title
 	{
 		font-size: 2.6vw;
 	}

 	.component__text
 	{
 		font-size: 2.2vw;
 	}
 }