
*
{
	margin: 0;
	padding: 0;
}

body
{
	background-color: #333;
	font-family: 'Open Sans', Helvetica, Arial, sans-serif;
}

hr
{
	width: 60%;
	margin: 5% 0 0 20%;
	opacity: .3;
}
.header
{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
	width: 100%;
}

.header__logo
{
	width: 1.7vw;;
	height: 1.7vw;
	margin-top: 3.5%;
	border: 6px solid #fff;
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
}

.header__title
{
	font-size: 3vw;
	margin: 3% 0 0 2%;
	color: #fff;
}

 @media screen and (max-width: 1024px)
 {
 	.header__logo
 	{
 		width: 3.5vw;
 		height: 3.5vw;
 	}

 	.header__title
 	{
 		font-size: 4.5vw;
 	}
 }

 @media screen and (max-width: 764px)
 {
 	.header__logo
 	{
 		width: 4.5vw;
 		height: 4.5vw;
 	}

 	.header__title
 	{
 		font-size: 6vw;
 	}
 }
.video
{
	width: 100%;
	margin-top: 5%;
}

.video__video
{
	width: 95%;
	margin-left: 2.5%;
}
.main__component
{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	margin: 7% 0 0 13%;
}

.component__img
{
	width: 13vw;
	height: 13.5vw;
	margin-top: 1%;
	border-radius: 50%;
}

.component__info
{
	width: 65%;
	border-radius: 25px;
	margin: -1% 0 0 5%;
	height: 16vw;
}

.info__title
{
	font-size: 1.8vw;
	margin: 5% 0 0 5%;
}

.info__text
{
	font-size: 1.4vw;
	color: #CCCCCC;
	margin: 2% 4% 0 5%;
}

.info__date
{
	font-size: 1.3vw;
	margin: 2% 0 0 70%;
}

.dark
{
	background-color: #262626;
}

.grey
{
	background-color: #404040;
}

.greenTxt
{
	color: #99CC66;
}

.blueTxt
{
	color: #33CCCC;
}

.yellowTxt
{
	color: #FFFF66;
}

.orangeTxt
{
	color: #CC9966;
}


 @media screen and (max-width: 1024px)
 {
 	.component__img
 	{
 		width: 20vw;
 		height: 20.5vw;
 		margin-top: 5%;
 	}

 	.component__info
 	{
 		height: 30vw;
 	}

 	.info__title
 	{
 		font-size: 3vw;
 		margin-top: 8%;
 	}

 	.info__text
 	{
 		font-size: 2vw;
 	}

 	.info__date
 	{
 		font-size: 1.8vw;
 		margin: 5% 0 0 55%;
 	}
 }

 @media screen and (max-width: 764px)
 {
 	.main__component
 	{
 		-webkit-flex-direction: column;
 		        flex-direction: column;
 		-webkit-align-items: center;
 		        align-items: center;
 		margin-left: 0;
 	}

 	.component__img
 	{
 		width: 23vw;
 		height: 23.5vw;
 		margin-left: 0;
 	}

 	.component__info
 	{
 		width: 80%;
 		height: 45vw;
 		margin: 7% 0 0 0;
 	}

 	.info__title
 	{
 		font-size: 4vw;
 		margin-top: 10%;
 	}

 	.info__text
 	{
 		font-size: 3vw;
 	}

 	.info__date
 	{
 		font-size: 2.5vw;
 		margin: 5% 0 0 55%;
 	} 

 }
main
{
	width: 100%;
	margin-top: 5%;
}

.main__title
{
	font-size: 2.5vw;
	margin-left: 7%;
	color: #CCCCCC;
}

 @media screen and (max-width: 1024px)
 {
 	.main__title
 	{
 		font-size: 4vw;
 	}
 }

 @media screen and (max-width: 764px)
 {
 	.main__title
 	{
 		font-size: 5vw;
 	}
 }
.AbUs_component
{
	width: 25%;
	margin: 5% 0 0 6%;
	color: #CCCCCC;
}

.component__title
{
	font-size: 1.8vw;
}

.component__text
{
	font-size: 1.35vw;
	margin-top: 5%;
}

 @media screen and (max-width: 1024px)
 {
 	.component__title
 	{
 		font-size: 2.2vw;
 	}

 	.component__text
 	{
 		font-size: 2vw;
 	}
 }

 @media screen and (max-width: 764px)
 {
	
	.AbUs_component
	{
		width: 70%;
		margin-left: 15%;
	}

 	.component__title
 	{
 		font-size: 2.6vw;
 	}

 	.component__text
 	{
 		font-size: 2.2vw;
 	}
 }
.AbUs
{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}
.contactUs
{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	width: 87%;
	margin: 5% 0 0 5%;
}

.contactUs__location
{
	width: 45%;
}

.location__title,
.form__title
{
	font-size: 1.8vw;
	color: #CCCCCC;
}

.contactUs__form
{
	width: 45%;
	margin-left: 7%;
}

.form
{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin-top: 5%;
}

.form__name,
.form__email
{
	font-size: 1.4vw;
	width: 45%;
	padding: 2.5% 0 2.5% 2%;
	background-color: transparent;
	color: #CCC;
	border: 1px solid #666666;
	outline: none;
}

.form__text
{
	font-size: 1.4vw;
	width: 98%;
	margin-top: 5%;
	padding: 2% 0 23% 2%;
	color: #CCC;
	background-color: transparent;
	border: 1px solid #666666;
	outline: none;
	resize: none;
}

.form__btn
{
	font-size: 1.5vw;
	width: 32%;
	padding: 2.5% 0 2.5% 0;
	margin-top: 5%;
	background-color: transparent;
	border: 1px solid #cccccc;
	color: #cccccc;
	outline: none;
	cursor: pointer;
	transition: .3s;
}

.form__btn:hover
{
	background-color: #fff;
	color: #000000;
}

input::-webkit-input-placeholder {
    color: #ccc;
} 
input::-moz-placeholder {
    color: #ccc;
}

textarea::-webkit-input-placeholder {
    color: #ccc;
} 
textarea::-moz-placeholder {
    color: #ccc;
}

iframe
{
	width: 100%;
	height: 24.5vw;
	margin-top: 5%;
}



 @media screen and (max-width: 1024px)
 {

 	iframe
 	{
 		height: 50vw;
 	}

	.contactUs
	{
		width: 70%;
		margin-left: 15%;
	}

	.location__title,
	.form__title
	{
		font-size: 2.2vw;
	}

	.contactUs__location
	{
		width: 100%;
	}

	.contactUs__form
	{
		width: 100%;
		margin: 5% 0 0 0;
	}

	.form
	{
		margin-top: 0;
	}

	.form__name,
	.form__email
	{
		font-size: 1.8vw;
		width: 100%;	
		margin: 3% 0 0 0;
	}

	.form__text
	{
		font-size: 1.8vw;
		margin-top: 3%;
	}

	.form__btn
	{
		font-size: 1.8vw;
		width: 25%;
	}
 }

 @media screen and (max-width: 764px)
 {
	.contactUs
	{
		width: 90%;
		margin-left: 5%;
	}

	.location__title,
	.form__title
	{
		font-size: 2.6vw;
	}
	.form__name,
	.form__email,
	.form__btn,
	.form__text
	{
		font-size: 2.2vw;
	}
 } 
.footer
{
	font-size: 1.3vw;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
	width: 100%;
	margin-top: 3%;
	padding-bottom: 5%;
}

.footer__text,
.footer__link
{
	color: #ccc;
}

.footer__link
{
	margin-left: .5%;
	text-decoration: none;
}

.footer__link:hover
{
	text-decoration: underline;
}

 @media screen and (max-width: 1024px)
 {
 	.footer
 	{
 		font-size: 1.6vw;
 	}
 }

 @media screen and (max-width: 764px)
 {
 	.footer
 	{
 		font-size: 2.2vw;
 	}
 }
