.contactUs
{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 87%;
	margin: 5% 0 0 5%;
}

.contactUs__location
{
	width: 45%;
}

.location__title,
.form__title
{
	font-size: 1.8vw;
	color: #CCCCCC;
}

.contactUs__form
{
	width: 45%;
	margin-left: 7%;
}

.form
{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-top: 5%;
}

.form__name,
.form__email
{
	font-size: 1.4vw;
	width: 45%;
	padding: 2.5% 0 2.5% 2%;
	background-color: transparent;
	color: #CCC;
	border: 1px solid #666666;
	outline: none;
}

.form__text
{
	font-size: 1.4vw;
	width: 98%;
	margin-top: 5%;
	padding: 2% 0 23% 2%;
	color: #CCC;
	background-color: transparent;
	border: 1px solid #666666;
	outline: none;
	resize: none;
}

.form__btn
{
	font-size: 1.5vw;
	width: 32%;
	padding: 2.5% 0 2.5% 0;
	margin-top: 5%;
	background-color: transparent;
	border: 1px solid #cccccc;
	color: #cccccc;
	outline: none;
	cursor: pointer;
	transition: .3s;
}

.form__btn:hover
{
	background-color: #fff;
	color: #000000;
}

input::-webkit-input-placeholder {
    color: #ccc;
} 
input::-moz-placeholder {
    color: #ccc;
}

textarea::-webkit-input-placeholder {
    color: #ccc;
} 
textarea::-moz-placeholder {
    color: #ccc;
}

iframe
{
	width: 100%;
	height: 24.5vw;
	margin-top: 5%;
}



 @media screen and (max-width: 1024px)
 {

 	iframe
 	{
 		height: 50vw;
 	}

	.contactUs
	{
		width: 70%;
		margin-left: 15%;
	}

	.location__title,
	.form__title
	{
		font-size: 2.2vw;
	}

	.contactUs__location
	{
		width: 100%;
	}

	.contactUs__form
	{
		width: 100%;
		margin: 5% 0 0 0;
	}

	.form
	{
		margin-top: 0;
	}

	.form__name,
	.form__email
	{
		font-size: 1.8vw;
		width: 100%;	
		margin: 3% 0 0 0;
	}

	.form__text
	{
		font-size: 1.8vw;
		margin-top: 3%;
	}

	.form__btn
	{
		font-size: 1.8vw;
		width: 25%;
	}
 }

 @media screen and (max-width: 764px)
 {
	.contactUs
	{
		width: 90%;
		margin-left: 5%;
	}

	.location__title,
	.form__title
	{
		font-size: 2.6vw;
	}
	.form__name,
	.form__email,
	.form__btn,
	.form__text
	{
		font-size: 2.2vw;
	}
 } 