.main__component
{
	display: flex;
	flex-direction: row;
	margin: 7% 0 0 13%;
}

.component__img
{
	width: 13vw;
	height: 13.5vw;
	margin-top: 1%;
	border-radius: 50%;
}

.component__info
{
	width: 65%;
	border-radius: 25px;
	margin: -1% 0 0 5%;
	height: 16vw;
}

.info__title
{
	font-size: 1.8vw;
	margin: 5% 0 0 5%;
}

.info__text
{
	font-size: 1.4vw;
	color: #CCCCCC;
	margin: 2% 4% 0 5%;
}

.info__date
{
	font-size: 1.3vw;
	margin: 2% 0 0 70%;
}

.dark
{
	background-color: #262626;
}

.grey
{
	background-color: #404040;
}

.greenTxt
{
	color: #99CC66;
}

.blueTxt
{
	color: #33CCCC;
}

.yellowTxt
{
	color: #FFFF66;
}

.orangeTxt
{
	color: #CC9966;
}


 @media screen and (max-width: 1024px)
 {
 	.component__img
 	{
 		width: 20vw;
 		height: 20.5vw;
 		margin-top: 5%;
 	}

 	.component__info
 	{
 		height: 30vw;
 	}

 	.info__title
 	{
 		font-size: 3vw;
 		margin-top: 8%;
 	}

 	.info__text
 	{
 		font-size: 2vw;
 	}

 	.info__date
 	{
 		font-size: 1.8vw;
 		margin: 5% 0 0 55%;
 	}
 }

 @media screen and (max-width: 764px)
 {
 	.main__component
 	{
 		flex-direction: column;
 		align-items: center;
 		margin-left: 0;
 	}

 	.component__img
 	{
 		width: 23vw;
 		height: 23.5vw;
 		margin-left: 0;
 	}

 	.component__info
 	{
 		width: 80%;
 		height: 45vw;
 		margin: 7% 0 0 0;
 	}

 	.info__title
 	{
 		font-size: 4vw;
 		margin-top: 10%;
 	}

 	.info__text
 	{
 		font-size: 3vw;
 	}

 	.info__date
 	{
 		font-size: 2.5vw;
 		margin: 5% 0 0 55%;
 	} 

 }