.header
{
	display: flex;
	flex-direction: row;
	justify-content: center;
	width: 100%;
}

.header__logo
{
	width: 1.7vw;;
	height: 1.7vw;
	margin-top: 3.5%;
	border: 6px solid #fff;
	transform: rotate(45deg);
}

.header__title
{
	font-size: 3vw;
	margin: 3% 0 0 2%;
	color: #fff;
}

 @media screen and (max-width: 1024px)
 {
 	.header__logo
 	{
 		width: 3.5vw;
 		height: 3.5vw;
 	}

 	.header__title
 	{
 		font-size: 4.5vw;
 	}
 }

 @media screen and (max-width: 764px)
 {
 	.header__logo
 	{
 		width: 4.5vw;
 		height: 4.5vw;
 	}

 	.header__title
 	{
 		font-size: 6vw;
 	}
 }