.footer
{
	font-size: 1.3vw;
	display: flex;
	flex-direction: row;
	justify-content: center;
	width: 100%;
	margin-top: 3%;
	padding-bottom: 5%;
}

.footer__text,
.footer__link
{
	color: #ccc;
}

.footer__link
{
	margin-left: .5%;
	text-decoration: none;
}

.footer__link:hover
{
	text-decoration: underline;
}

 @media screen and (max-width: 1024px)
 {
 	.footer
 	{
 		font-size: 1.6vw;
 	}
 }

 @media screen and (max-width: 764px)
 {
 	.footer
 	{
 		font-size: 2.2vw;
 	}
 }